module.exports = [{"title":"OSPC","content":["<a href='https://www.amazon.in/' target='_blank'><p  style='text-align:center;'>Intern Sponsor</p><img src='../amazon.jpg' alt='Amazon'/></a><br/>Is your definition of fun going to HackerRank or SPOJ?<br/>Do you end your sentences with a semicolon instead of a period?<br/>Are you one of those people who get high when all your test cases pass?<br/><br/>To all those who believe in the motto <i>‘Talk is Cheap, Show me the Code’</i>, then OSPC awaits you.","<b>Round 1</b> <ul><li>Pen and Paper</li><li>15 one mark questions</li><li>8 coding questions with varying marks based on difficulty (4 easy +4 hard)</li></ul><b>Round 2 </b> <ul><li>Lab Session</li><li>5 coding questions (1 easy + 2 medium + 2 hard)</li><li>Duration: 1.5 to 2 hours</li><li>Only 10 teams will be shortlisted</li></ul>","<ul><li>Maximum three per team. Lone wolves are allowed.</li><li>Teams consisting of participants from different colleges is allowed.</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Makisha - 96290 78175<br/>Harini - 94448 50785"]},{"title":"Game of Nerds","content":["<p><em>&ldquo;When you play the game of thrones, you either win or die&rdquo;</em></p><p style='text-align: right;'><em>-Cersei Lannister</em></p><p>Sharpen your minds and heighten your focus, it&rsquo;s time for battle. Put all your experiences through the tumultuous journey that is college life to the test. Prepare yourself to face pass conquered enemies &ndash; Algorithms, Data Structures, Networks and the like. For only one can win the Game of Nerds and claim their place on the throne.</p>","<b>Round 1:</b><ul><li>Multiple Choice Questions (30 Questions)</li><li>Duration: 30 mins</li></ul><b>Round 2:</b><ul><li>Questions will be based on Debugging, DBMS, OS and general coding.</li><li>Duration: 45 minutes</li><li>Only 7-10 teams will qualify to this round</li></ul>","<ul><li>Maximum two per team. Lone wolves are allowed.</li><li>Teams consisting of participants from different colleges is allowed.</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification.</li><li>Decisions taken by the organizer are final and binding.</li></ul>","Agashya - 98659 17474<br/>Priyanka - 84288 19570"]},{"title":"Cyber Wars","content":["<a href='https://www.gigamon.com/' target='_blank'><p  style='text-align:center;'>Intern Sponsor</p><img src='../gigamon.png' alt='Gigamon'/></a><i>'Sticks and Stones may break my bones, but ciphers and cryptograms excite me.'</i><br/>If your devious litte mind deviates toward any and all things cryptographic, then this event is precisely for you. Put your mind to the test and uncover the deepest secrets in the realm of cryptic codes. Encrypt, Decrypt and repeat to your heart’s content in Abacus’ Cyberwars!","<p><strong>Round 1</strong> &ndash; Pen and Paper</p><p>30 one mark questions + four tie breaker questions</p><p>Duration - 40mins</p><p><strong>Round 2</strong> &ndash; Lab Session</p><p>Questions based on Message Passing and socket programming that include cryptographic techniques</p><p>Duration &ndash; 45 mins to 1 hour</p><p>Only 8 teams will be shortlisted to Round 2</p>","<ul><li>Maximum 2 per team. Lone wolves are allowed.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification.</li><li>Decisions taken by the organizer are final and binding.<u></u></li></ul>","Ajay - 88833 39409<br/>Nambi Kannan - 85267 57626"]},{"title":"Debugging","content":["<a href='https://www.freshworks.com/' target='_blank'><p  style='text-align:center;'>Intern Sponsor</p><img src='../freshworks.png' alt='Freshworks'/></a><i>‘When debugging, novices insert corrective code; experts remove defective code’</i><br>Are you the resident bug remover of your class? Does removing that one error among thousands of lines of code give you an adrenaline rush? Put your skills to the test with Abacus’ debugging contest - may the best exterminator win.","<p><strong>Round 1</strong>&nbsp;</p><p>Pen and Paper</p><p>15 one mark questions</p><p>3 pseudo code based questions with varying marks based on difficulty</p><p><strong>Round 2</strong>&nbsp;</p><p>Lab Session</p><p>3 coding questions (1 debugging +1 reverse coding + 1 obfuscated code (write code and crack other teams&rsquo; code))</p><p>Duration &ndash; 65 minutes (15mins + 20 mins + 30mins (10 mins to code + 20 mins to crack other's code))</p><p>Only 6 teams will be shortlisted to Round 2</p>","<ul><li>Maximum 2 per team. Lone wolves are allowed.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Dinesh - 90945 13434<br/>Sivaram kumar M - 8220762765"]},{"title":"Alohomora","content":["If databases, deadlocks and queries are just a game<br/> then alohomora will be your climb to fame. <br/>Use your spells to conjure the key,<br/>Resolve those pesky deadlocks glee,</br>Make the database dance to your tune<br/>and victory will come to you as a boon.","<b>Round 1</b><ul><li>Prelims: (Pen and Paper Round)</li><li>25 Questions + 3 tie breaker questions</li><li>Questions will be of three types - MCQs, single word answers, or short answers.</li><li>Duration: 45 minutes</li></ul><br/><b>Round 2 </b> <ul><li>Finals: (Lab Session)</li><li>There will be 6 different databases consisting of 15 questions each</li><li>Time limit: 1 hour</li><li>6 teams will be selected for the final round.</li></ul>","<ul><li>Extra 15 mins will be given for database creation and insertion.</li><li>Maximum two per team. Lone wolves are allowed.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Shruthi - 99442 63196<br/>Akila - 9840781162<br/>Mugilan - 8526407163<br/>Sukin - 9944513445"]},{"title":"Webaholic","content":["<a href='http://www.wekancode.com/' target='_blank'><p  style='text-align:center;'>Intern Sponsor</p><img src='../wekancode.png' alt='WeKanCode'/></a><br/>What’s the world without websites? <br/>Sculpt your site with HTML and JavaScript and add the final flourish with CSS. Wow the crowd with your eye catching site and prove yourself to be the web design wizard you are.","<div><div><div><strong><u>Format:</u></strong><div><div><strong>Round 1&nbsp;</strong><strong>(28/2/2018 : FN)</strong></div><ul><li>26 question= (16 front end and backend questions) + (10 algo,ds,maths questions )with varying marks and difficult.</li></ul><div><strong>Round 2&nbsp;</strong><strong>(28/2/2018 :AN)</strong></div><ul><li>participants have to develop a application based on the given theme</li><li>Duration &ndash; maximum of 4 hours&nbsp;</li><li>Only 8 teams will be shortlisted for Round 2.</li></ul></div></div></div></div>","<div><div><strong><u>Rules:</u></strong></div><div><ul><li>Minimum of 2, Maximum of 3 per team.</li><li>Teams consisting of participants from different colleges is allowed</li><li>For the round 2 each team will be given with 2 desktops with internet connection</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul></div></div>","Sakthi Prasath - 81100 10630<br/>Santha Kumar - 96005 38775"]},{"title":"3 Musketeers","content":["Athos. Porthos. Aramis.<br/>Gather your comrades in arms, as you face the challenges that lie ahead. Pace yourselves in this relay race against time, as we put your teamwork to the test!","<p><strong>Round 1</strong></p><p>Pen and Paper</p><p>All questions must be solved by providing 3 different approaches</p><p>Duration: 45 minutes</p><p><strong>Round 2</strong></p><p>Lab Session</p><p>Number of questions: 3</p><p>Duration: 1 hour</p><p>For the first 20 minutes, each member will start solving one question.</p><p>After the first 20 minutes, the questions will be swapped among the members and each should continue with their newly assigned code.</p><p>The same will be followed for the next 20 minutes.</p><p>&nbsp;</p><p>Only 5 to 6 teams will be shortlisted for Round 2.</p>","<ul><li>Only teams of 3 are allowed.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Gayathri - 80560 78716<br/>Priyavarshini - 98946 42863<br/>Sivaram Kumar - 8220762765"]},{"title":"Flash","content":["Get ready for a feast for the eyes as we watch your fingers dance across the keys. As the seconds pass, wrack your brain to solve these deceptively difficult problems. Be the first to crack the code and beat the clock! ","<p><strong>ROUND 1: </strong></p><p>Pen and Paper</p><p>Question Type: Objective questions (30)</p><p>Time: 30 mins</p><p>Questions on various topics. Higher priority will be given for networks questions. Domains: Networks, C and C++ (Pointers, Arrays, Linked List, Tree, Graph, Functions and Control Structures)</p><p>&nbsp;</p><p><strong>ROUND 2: </strong></p><p>Competitive Coding</p><p>Question Type: Problem Solving (1 &ndash; based on Networks) + 2 Competitive Coding</p><p>Participants must crack the first network question to proceed to the next questions.</p><p>One participant from each team can start coding. If help is required, team mates may assist. However, marks are awarded according to the time at which they join the first participant.</p><p>&nbsp;</p><p><strong>ROUND 3:</strong></p><p>Time will be given according to the complexity of the questions.</p><p>Question Type: Time Constraint Questions (25)</p><p>Domains: Networks, Algorithms, TOC, OS, CA, DBMS, C programming</p>","<ul><li>Teams of 3 only.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Arul Roja - 97886 41525"]},{"title":"Code Hunt","content":["Treasure Hunting 101: X marks the spot.<br/>Code Hunting 101 : Obfuscated code marks the spot.<br/>Follow the trail and compile the clues as you trudge through the trails of Abacus’ Code Hunt. Beware of the dangers that lie in your wake – Polymorphism, Abstraction and Recursion are not to be trifled with. Brave the storm and find riches galore as you capture the clue obscured by our code!","<p><strong>Round 1</strong></p><p>No of questions: 5</p><p>Duration: 2-3 hours</p><p>Each of the 5 questions is a programming question. The correct solution to each question will lead to the next location of the clue and finally, the final treasure.</p>","<ul><li>Only 3 per team.</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li><li>Participants are requested to bring their own laptops for the event.</li></ul><p>P.S. <strong>Laptop is required for the finals.</strong></p>","Vishvesh Thangamani - 91596 93399 <br/>Aravindh - 99412 36630"]},{"title":"Hello World","content":["If(Student.year&equals;&equals;1){<br/>Cout&lt;&lt;”Then this event is for you!”;<br/>}<br/>Calling all first years! Hello World is your first step into the world of Tech.Come join in to find out what Abacus has in store for you. </br>Hint : Loads of fun, frolic and amazing prizes.","<b>Round 1 </b><ul><li> Pen and Paper</li><li>50 one mark questions with varying difficulty</li></ul><b>Round 2</b> <ul><li> Lab Session</li><li>5 coding questions (2 easy + 2 medium + 1 hard)</li><li>Duration-1.5 to 2 hours based on schedule</li><li>Only 10 teams will be shortlisted to Round 2.</li></ul>","<ul><li>Only first years are allowed.</li><li>Only Teams of 2 or 3 are allowed</li><li>Teams consisting of participants from different colleges is allowed</li><li>Use of the internet is not allowed.</li><li>Any form of malpractice will lead to immediate disqualification</li><li>Decisions taken by the organizer are final and binding</li></ul>","Arun - 80123 64643<br/>Thirunavukarasu - 94867 52704"]},{"title":"Code Man","content":["<p>If black jack and poker have become too main stream for you, Brace yourselves for code gambling. Show us your hand and code under constraints. Prove that you’re the best at striking a deal in this cybernetic gambling game.</p>","<p><strong>Round 1:Paper and Pen</strong></p><ul><li>Code out the problems but under given limits and constraints.</li><li>Fail the given program's logic by finding out the corner test cases.</li><li>Optimize the operations, execution time and spac and lot more.</li></ul><p>Number of Questions : 25<br />Duration: 45 minutes</p><p><br /><strong>Round 2: Lab session</strong></p><ul><li>Independent Auction for programming.</li><li>Buy DataStructures and Algorithimic paradigms that suits the needs of the programs at hand.</li></ul><p>No of questions : 10<br />Duration : 75 minutes.</p>","<ul><li>Teams of two. Lone wolves are also encouraged.</li><li>Multiple solutions for same question is encouraged. Come up with as many solutions as possible.</li><li>Use of internet is strictly prohibited.</li><li>Organisers decision is final and binding.</li></ul>","<p>Mohamed Yilmaz - 9677258450<br />Monisha - 9698156225<br />Ramkumar - 9677867073<br />Pavithra - 9976611624</p>"]},{"title":"Re'present","content":["<p><em>&lsquo;The only place where your dream becomes impossible is in your dreams&rsquo;</em></p><p>Are you an eccentric creator with the ability to think out of the box? Then fire up that grey matter and show th world what you&rsquo;re capable of. Come up with an idea that can change life as we know it and use your potential to bring your idea to life with Abacus&rsquo; Re&rsquo;Present. Wow the judges with your innovative ideas in the form of a model, prototype, apps or any software product.<br/>Date: Feb 28 and Mar 1</p>","<p><strong>Round 1:</strong></p><ul><li>Participants have to mail the abstract electing their topic of interest provided in the list of domains.</li><li>Create a document containing the following details</li></ul><ol><li>Name of Team</li><li>Name of Team-Leader</li><li>Number of members in the team</li><li>Details of Team members (including Team Leader, Name, Email ID, Contact number)</li><li>Title of the paper</li><li>Abstract</li><li>Innovation (explain how your design is unique)</li><li>Underlying principles/technical details</li></ol><ul><li>Participants should mail the above document to <a href='mailto:paperpresentation@abacus.org.in'>paperpresentation@abacus.org.in</a></li><li>Last date for submission is 15th Feb, 2018.</li></ul><p><strong>Round2:</strong></p><ul><li>On the basis of the submissions received, respective teams will be selected for the final presentation.</li><li>A confirmation mail will be sent to the selected teams to take part in the event during ABACUS&rsquo;18 and present their paper.</li><li>Presentations should not exceed 15 minutes.</li><li>The maximum time allotted for single presentation is 12 minutes, followed by a question answer session section for 3 minutes. Additional time will be considered a penalty and affect scoring.</li><li>The presentation will be judged on the basis of innovation, in depth knowledge of the field and presentation skills.</li></ul><p><strong>DOMAINS</strong></p><p>1) Natural User Interface (NUI)</p><p>2) Human Computer Interface (HCI)</p><p>3) Blockchain</p><p>4) Virtual Reality(VR) &amp; Augmented Reality(AR)</p><p>5) Computational Linguistics</p><p>6) Brain Computer Interface (BCI)</p><p>7) Internet of Things (IoT)</p><p>8) Smart Home Environment (SHE)</p><p>9) Advanced Machine Learning(ML)</p><p>10) Deep Learning (DT)</p><p>11) Data Mining</p><p>12) Software defined Networks</p><p>13) Virtualization Technologies</p><p>14) Wireless Technologies</p><p>15) Cyber Crime and Security</p>","<ul><li>Maximum number of participants per team should not exceed 3.</li><li>Participants may be part of only one team at a time.</li><li>Each participant should bring their respective college ID proof.</li><li>Teams may consist of participants from different colleges.</li><li>Plagiarism is not encouraged.</li><li>Any number of teams can come from same college.</li><li>In case of any discrepancy, decision made by the judge is final.</li></ul>","<p>Send your abstracts to : <a href='mailto:paperpresentation@abacus.org.in'>paperpresentation@abacus.org.in </a></p><p>For more details contact: Vijay 9489029770 <br/>Srinath  9790893949</p>"]}]

